@tailwind base;
@tailwind components;
@tailwind utilities;

/* ✅ Предотвращение FOUC - черный фон сразу */
html, body, #root {
  background-color: #000000 !important;
  color: white !important;
  margin: 0;
  padding: 0;
}

html {
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  touch-action: manipulation;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: none;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  background: #000000 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

/* ✅ Максимальная стабилизация canvas для мобилок */
canvas {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  pointer-events: none !important;
  touch-action: none !important;
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  user-select: none !important;
  transform: translate3d(0, 0, 0) !important;
  -webkit-transform: translate3d(0, 0, 0) !important;
  backface-visibility: hidden !important;
  -webkit-backface-visibility: hidden !important;
  image-rendering: pixelated !important;
  -webkit-image-rendering: pixelated !important;
  transform-origin: top left;
  -webkit-transform-origin: top left;
}

/* ✅ Исправления для iOS Safari */
@supports (-webkit-touch-callout: none) {
  html {
    height: -webkit-fill-available;
  }

  body {
    min-height: 100vh;
    min-height: -webkit-fill-available;
  }

  canvas {
    position: fixed !important;
    -webkit-transform: translate3d(0, 0, 0) !important;
    transform: translate3d(0, 0, 0) !important;
  }
}

/* ✅ Предотвращение zoom и нестабильности на мобилках */
@media screen and (max-width: 768px) {
  * {
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
  }

  html {
    -webkit-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }

  canvas {
    width: 100vw !important;
    height: 100vh !important;
    max-width: 100vw !important;
    max-height: 100vh !important;
  }
}

/* ✅ Исправления для заголовков в блоге */
.prose h3 {
  color: #00c9ff !important;
  font-size: 1.25rem !important;
  font-weight: 600 !important;
  margin-bottom: 0.75rem !important;
  margin-top: 1.5rem !important;
  font-family: monospace !important;
  letter-spacing: 0.05em !important;
  text-shadow: 0 0 5px rgba(0, 201, 255, 0.3) !important;
}

.prose h3:first-child {
  margin-top: 0 !important;
  color: white !important;
  font-size: 1.5rem !important;
  font-weight: 700 !important;
}

.prose p {
  color: rgb(212, 212, 216) !important;
  line-height: 1.75 !important;
  margin-bottom: 1rem !important;
  font-family: monospace !important;
  font-size: 0.95rem !important;
}

.prose strong {
  color: #00c9ff !important;
  font-weight: 600 !important;
}

.prose code {
  background: rgba(0, 201, 255, 0.1) !important;
  color: #00c9ff !important;
  padding: 0.125rem 0.25rem !important;
  border-radius: 0.25rem !important;
  font-size: 0.875rem !important;
}

.prose pre {
  background: rgba(0, 201, 255, 0.05) !important;
  border: 1px solid rgba(0, 201, 255, 0.2) !important;
  border-radius: 0.5rem !important;
  padding: 1rem !important;
  overflow-x: auto !important;
}

.prose pre code {
  background: transparent !important;
  padding: 0 !important;
  border-radius: 0 !important;
  color: rgb(212, 212, 216) !important;
}

/* ✅ Дополнительные исправления для стабильности */
#root {
  background-color: #000000 !important;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

/* ✅ Предотвращение белых вспышек */
* {
  background-color: transparent;
}

/* ✅ Исправления для навигации */
nav {
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
}

/* ✅ Исправления для производительности */
.will-change-transform {
  will-change: transform;
}

.gpu-accelerated {
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}
